import React from 'react';
import {retrieve_enrollments_by_token, update_enrollment, destroy_enrollment} from '../../middleend/fetchers.js';

import MaterialTable from 'material-table';


export default class EnrollmentUpdate extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            successful: '',
            response: '',
            loaded: false,
            enrollments: []
        }
        this.inlineStyle = {
            modal : {
            //   display: 'inline-block !important',
            //   marginLeft: 'auto',
            //   marginRight: 'auto'
            }
          };
    }
    componentDidMount() {
        const token = this.props.match.params.token; // match enrollment_id from 
        retrieve_enrollments_by_token(
            token,
            response => { this.setState({successful: true, enrollments: response, loaded:true }, () => console.log(this.state)); },
            error    => { this.setState({successful: false, response: error, loaded:true }); },
        );

    }

    confirm(e_id) {
        update_enrollment(
            e_id,
            response => { this.setState({successful: true, response: response, loaded:true }, () => console.log(this.state)); },
            error    => { this.setState({successful: false, response: error, loaded:true }); },
        );
    }

    cancel(e_id) {
        destroy_enrollment(
            e_id,
            response => { this.setState({successful: true, response: response, loaded:true }, () => console.log(this.state)); },
            error    => { this.setState({successful: false, response: error, loaded:true }); },
        );
    }
    render() {
        var enrollment_data = this.state.enrollments.map((e, i) => {
            return (
                {
                    name: e.child.first_name + " " + e.child.last_name,
                    activity_title: e.activity.title,
                    first_name: e.child.first_name,
                }
            );
        });
        return (
            <MaterialTable
                columns={[
                    { title: 'Activity', field: 'activity_title' },
                    { title: 'Student Name', field: 'name' },
                ]}
                data={enrollment_data}
                title="Pending Enrollments"
                isLoading={!this.state.loaded}
                actions={[
                    rowData => ({
                        icon: 'done_outline',
                        tooltip: 'Confirm ' + rowData.first_name + "'s enrollment in " + rowData.activity_title,
                        // disabled: rowData.birthYear >= 2000,
                        onClick: (event, rowData) => {
                            alert('You clicked user ' + rowData.name)
                        },
                        iconProps: {
                            style: {
                            fontSize: 30,
                            color: 'green',
                            },
                        },
                    }),
                    rowData => ({
                        icon: 'cancel',
                        tooltip: 'Cancel ' + rowData.first_name + "'s enrollment in " + rowData.activity_title,
                        onClick: (event, rowData) => {
                            alert('You canceled ' + rowData.name)
                        },
                        iconProps: {
                            style: {
                            fontSize: 30,
                            color: 'red',
                            },
                        },
                    }),
                ]}
            />
        );
    }
};